<template>
  <div class="clearfix" >
    <van-sidebar v-model="activeKey" class="fl">
      <van-sidebar-item v-for="(item , index) in aa" :key="index" :title="item.name" @click="on(item,index)"/>
    </van-sidebar>
    <div>
      <router-view/>
    </div>
  </div>

</template>

<script>

import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      activeKey: 0,
      aa:[]
    };
  },
  created(){
    this.activeKey = this.active
     this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      
      //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
      duration: 0
    });
    this.axios({
      method:'GET',
      url:'/class',
      params:{
        appkey:this.appkey
      }
    }).then(res=>{
        this.$toast.clear();
      for(let i = 0 ; i < res.data.result.length;i++){
        this.aa.push(res.data.result[i])
      }
      this.listes.push(this.aa[0].list)

    }).catch(err=>{
        this.$toast.clear();
         
    })
  },
  computed:{
    ...mapState(['listes','active'])
  },
  methods:{
    ...mapMutations(['listData','Active']),
    //点击侧栏分类，记录分类下标等数据
    on(item,index){
      this.listData(item.list)
      this.Active(index)
      
    }
  }
};
</script>

<style lang="less" scoped>

/deep/.van-sidebar-item{
  height: 57px;
}
/deep/ .van-sidebar-item--select::before{
 height: 100%;
 transition: all .5s;
  background-color: #1989fa;
}


</style>